import { Injectable } from '@angular/core';
import { BaseService } from "./base.service";
import { Expense } from '../models/expense.model';
import { GapiSession } from '../infrastructure/sessions/gapi.session';

@Injectable({
  providedIn: 'root'
})
export class ExpenseService extends BaseService {

   private static RANGE_RECURRENT_EXPENSES = 'ControlGastosRecurrentes!A2:G';
   private static RANGE_RECURRENT_EXPENSES_ADD_ROW = 'ControlGastosRecurrentes!A';
   private static RANGE_OCASIONAL_EXPENSES = 'ControlGastosOcasionales!A2:D';
   private static RANGE_OCASIONAL_EXPENSES_ADD_ROW = 'ControlGastosOcasionales!A';

   private _allExpenses: Array<Expense> = [];
   private _recurrenteExpenses: Array<Expense> = [];
   private _ocasionalExpenses: Array<Expense> = [];
   private _initExpenseServicePromise: PromiseLike<any>;
   private _initExpenseServicePromiseSolved: boolean;

   constructor(private gapiSession: GapiSession) {
      super();
   }

   public get allExpenses() : Array<Expense> {
      return this._allExpenses;
   }

   public get recurrenteExpenses() : Array<Expense> {
      return this._recurrenteExpenses;
   }

   public get ocasionalExpenses() : Array<Expense> {
      return this._ocasionalExpenses;
   }

   public initExpenseService() : PromiseLike<any> {
      if (!this._initExpenseServicePromise) {
         this._initExpenseServicePromise = this._loadRecurrentExpenses().then(() => {
            return this._loadOcasionalExpenses();
         });
      }

      if (!this._initExpenseServicePromiseSolved){
         return this._initExpenseServicePromise;
      }else{
         Promise.resolve("initialized");
      }
   }

   public saveExpense(expense : Expense) : PromiseLike<any> {

      // determine the row where to insert or update the info
      var listoOfExpenseses : Array<Expense> = [];
      var rowIndexToUpdate = ExpenseService.RANGE_OCASIONAL_EXPENSES_ADD_ROW + (expense.id > 0 ? (1 + expense.id) : (2 + this._ocasionalExpenses.length));
      if (expense.recurrente) {
         listoOfExpenseses = this._recurrenteExpenses;
         rowIndexToUpdate = ExpenseService.RANGE_RECURRENT_EXPENSES_ADD_ROW ;
         expense.montoReferencia = expense .montoPagado; // we save the last amount paid for each recurrent expense
      }else{
         listoOfExpenseses = this._ocasionalExpenses;
         rowIndexToUpdate = ExpenseService.RANGE_OCASIONAL_EXPENSES_ADD_ROW;
      }
      rowIndexToUpdate = rowIndexToUpdate + (expense.id > 0 ? (1 + expense.id) : (2 + listoOfExpenseses.length));

      return gapi.client.request(
         {
            method: 'POST',
            // Sets values in one or more ranges of a spreadsheet => https://developers.google.com/sheets/api/reference/rest/v4/spreadsheets.values/batchUpdate
            path: `https://sheets.googleapis.com/v4/spreadsheets/${BaseService.SPREADSHEET_ID}/values:batchUpdate`,
            body: {
               valueInputOption: BaseService.VALUE_INPUT_OPTION,
               data: [
                  {
                     range: rowIndexToUpdate,
                     values: [
                        expense.getRowForGastos()
                     ]
                  }
               ]
            }
         }
      ).then(function (response) {
         this._allExpenses.push(expense);
         listoOfExpenseses.push(expense);
      });
   }

   private _loadRecurrentExpenses(): Promise<any> {
      return this.gapiSession.initClient("Expense Service").then(() => 
         {
            return gapi.client.request(
               {
                  method: 'GET',
                  // Returns a range of values from a spreadsheet => https://developers.google.com/sheets/api/reference/rest/v4/spreadsheets.values/get
                  path: `https://sheets.googleapis.com/v4/spreadsheets/${BaseService.SPREADSHEET_ID}/values/${ExpenseService.RANGE_RECURRENT_EXPENSES}?valueRenderOption=${BaseService.VALUE_RENDER_OPTION}&dateTimeRenderOption=${BaseService.DATE_TIME_RENDER_OPTION}`
               }
            ).then((expenseRange)=>{
               this._initExpenseServicePromiseSolved = true;
               var expensesList = expenseRange.result["values"];
               if (expensesList) {
                  for (let i = 0; i < expensesList.length; i++) {
                     var expenseFromSheet = expensesList[i];
                     var expense = new Expense(expenseFromSheet[0], true, expenseFromSheet[1], expenseFromSheet[2], expenseFromSheet[4], expenseFromSheet[3], expenseFromSheet[5]);
                     this._recurrenteExpenses.push(expense);
                     this._allExpenses.push(expense);
                  }
               }
            });
         }
      );
   }

   private _loadOcasionalExpenses(): Promise<any> {
      return this.gapiSession.initClient("Product Service").then(() => 
         {
            return gapi.client.request(
               {
                  method: 'GET',
                  // Returns a range of values from a spreadsheet => https://developers.google.com/sheets/api/reference/rest/v4/spreadsheets.values/get
                  path: `https://sheets.googleapis.com/v4/spreadsheets/${BaseService.SPREADSHEET_ID}/values/${ExpenseService.RANGE_OCASIONAL_EXPENSES}?valueRenderOption=${BaseService.VALUE_RENDER_OPTION}&dateTimeRenderOption=${BaseService.DATE_TIME_RENDER_OPTION}`
               }
            ).then((res)=>{
               gastosRange => {
                  // necessary to run insice the zone in order to fire the change detection
                  //this.ngZone.run( () =>
                  // {
                  var expensesList = gastosRange.result["values"];
                  if (expensesList) {
                     for (let i = 0; i < expensesList.length; i++) {
      
                        var expenseFromSheet = expensesList[i];
                        // 0 = id, 1 = Concepto, 2 = Fecha Pagado, 3 = Monto Pagado
                        var gasto = new Expense(expenseFromSheet[0], false, expenseFromSheet[1], null, expenseFromSheet[2], null, expenseFromSheet[3]);
                        this._ocasionalExpenses.push(gasto);
                        this._allExpenses.push(gasto);
                     }
                  }
               }
            });
         }
      );
   }
}