import { Product } from './product.model';
import { OrderLine } from './order-line.model';
import { PaymentMethod } from './payment-method.model';

export class Order {

	public detalleOrdenList: Array<OrderLine> = [];
	public total: number = 0;
	public tipoPago = PaymentMethod.CASH;

	constructor(productos: Array<Product>){
		for (var p in productos){
			this.detalleOrdenList.push(new OrderLine(productos[p], this));
		}
	}
	
	reset(){
		for (var detalleOrden in this.detalleOrdenList){
			this.detalleOrdenList[detalleOrden].tipoPago = PaymentMethod.CASH;
			this.detalleOrdenList[detalleOrden].vendido = 0;
			this.detalleOrdenList[detalleOrden].feria = 0;
			this.detalleOrdenList[detalleOrden].calcularTotal();
		}
	}

	public calcularTotal(): void{
		this.total = 0;
		for (var detalleOrden in this.detalleOrdenList){
			this.total += this.detalleOrdenList[detalleOrden].total;
		}
	}

	public getRowsForVenta() : Array<any> {
		let newVentas = new Array();
		let now = new Date();
		let date = now.toLocaleDateString() + " " + now.toLocaleTimeString();
		for (var detalle in this.detalleOrdenList) {
			var detalleOrden = this.detalleOrdenList[detalle];
			if (detalleOrden.vendido > 0){
				var row = new Array();
				row.push(date);
				row.push(this.tipoPago == PaymentMethod.CASH ? "Efectivo" : "Tarjeta");
				row.push(detalleOrden.producto.id + "");
				row.push(detalleOrden.producto.precio + "");
				row.push(detalleOrden.vendido + "");
				row.push((detalleOrden.vendido * detalleOrden.producto.precio) + "");
				newVentas.push(row);
			}
		}
		return newVentas;
	}

	public toggleTipoPago(): void{
		switch (this.tipoPago){
			case PaymentMethod.CASH:
				this.tipoPago = PaymentMethod.CREDIT_DEBIT;
				break;
			case PaymentMethod.CREDIT_DEBIT:
				this.tipoPago = PaymentMethod.CASH;
				break;
		}
	}
}