import { Injectable } from '@angular/core';
import { GapiSession } from 'src/infrastructure/sessions/gapi.session';
import { BaseService } from './base.service';
import { ProductService } from './product.service';
import { WastedProduct } from 'src/models/wasted-product.model';
import { WastedLine } from 'src/models/wasted-line.model';

@Injectable({
  providedIn: 'root'
})
export class WastedService extends BaseService {

   private static RANGE_WASTED = 'ControlPerdidas!A2:C';
   private static RANGE_WASTED_ADD_ROW = 'ControlPerdidas!A2';

   private _allWasted: Array<WastedProduct> = [];
   private _initWastedServicePromise: PromiseLike<any>;
   private _initWastedServicePromiseSolved: boolean;

   constructor(private gapiSession: GapiSession, private productService: ProductService) {
      super();
   }

   public get allWasted() : Array<WastedProduct> {
      return this._allWasted;
   }

   public initWastedService() : PromiseLike<any> {
      if (!this._initWastedServicePromise) {
         this._initWastedServicePromise = this.productService.initProductService().then(()=> {return this._loadWastedLines();});
      }

      if (!this._initWastedServicePromiseSolved){
         return this._initWastedServicePromise;
      }else{
         Promise.resolve("initialized");
      }
   }

   private _loadWastedLines(): Promise<any> {
      return this.gapiSession.initClient("Wasted Service").then(() => 
         {
            return gapi.client.request(
               {
                  method: 'GET',
                  // Returns a range of values from a spreadsheet => https://developers.google.com/sheets/api/reference/rest/v4/spreadsheets.values/get
                  path: `https://sheets.googleapis.com/v4/spreadsheets/${BaseService.SPREADSHEET_ID}/values/${WastedService.RANGE_WASTED}?valueRenderOption=${BaseService.VALUE_RENDER_OPTION}&dateTimeRenderOption=${BaseService.DATE_TIME_RENDER_OPTION}`
               }
            ).then((productionRange)=>{
               this.productService.allOwnedProducts.forEach(
                  p => {
                     this._allWasted.push(new WastedProduct(p));
                  }
               );

               this._initWastedServicePromiseSolved = true;
               var productionList = productionRange.result["values"];
               if (productionList) {
                  
                  for (let i = 0; i < productionList.length; i++) {
                     var productionLineFromSheet = productionList[i];

                     // 0 = Id Producto, 2 = Fecha,  1 = Perdido
                     var wastedByProduct = this._allWasted.find(pl => pl.product.id == productionLineFromSheet[0]);
                     if (wastedByProduct){
                        var wastedLine = new WastedLine(wastedByProduct.product, productionLineFromSheet[1], productionLineFromSheet[2]);
                        wastedByProduct.addWastedLine(wastedLine);
                     }else{
                        console.warn("A WastedProduct was not found. Was the ControlPerdidas modified manually?. Check all products in that list are of Tipo Propio");
                     }
                  }
               }
            });
         }
      );
   }

   public saveWasted(): PromiseLike<any> {

      let wastedListToSave = new Array();
      this._allWasted.forEach( p => {
         if (p.wasted > 0){
            p.addWastedLine(new WastedLine(p.product, new Date(), p.wasted));
            p.wasted = 0;
         }
         p.wastedLines.forEach(
            pl => {
               var row = new Array();
               row.push(pl.product.id);
               row.push(pl.date);
               row.push(pl.wasted);
               wastedListToSave.push(row);
            }
         );
      });

		// Registers the Efectivo and the Tarjeta
		return gapi.client.request(
			{
            method: 'POST',
            // Sets values in one or more ranges of a spreadsheet => https://developers.google.com/sheets/api/reference/rest/v4/spreadsheets.values/batchUpdate
            path: `https://sheets.googleapis.com/v4/spreadsheets/${BaseService.SPREADSHEET_ID}/values:batchUpdate`, 
            body: {
               valueInputOption: BaseService.VALUE_INPUT_OPTION,
               data: [
                  {
                     range: WastedService.RANGE_WASTED_ADD_ROW,
                     values: wastedListToSave
                  }
               ]
            }			
         }
		);
	}
}