import { Component, OnInit, NgZone } from '@angular/core';
import { WastedProduct } from 'src/models/wasted-product.model';
import { WastedService } from 'src/services/wasted.service';

@Component({
  selector: 'app-wasted-component',
  templateUrl: './wasted.component.html',
  styleUrls: ['./wasted.component.css']
})
export class WastedComponent implements OnInit {

   private _processing: boolean = false;
   private _allWasted: Array<WastedProduct> = [];

   constructor(private wastedService: WastedService, private ngZone: NgZone) { }

   public get allWasted(){
      return this._allWasted;
   }

   public get processing(){
      return this._processing;
   }

   ngOnInit() {
      this.wastedService.initWastedService().then(()=>{
         this.ngZone.run(() => {
            this._allWasted = this.wastedService.allWasted;
         });}
      );
   }

   public saveWasted(){
      this.wastedService.saveWasted().then(()=>{
         this.ngZone.run(() => {
            this._allWasted = this.wastedService.allWasted;
         });}
      );
   }
}