import { Product } from './product.model';
import { WastedLine } from './wasted-line.model';

export class WastedProduct {

   private _wastedLines: Array<WastedLine> = [];
   private _wasted: number = 0;
   private _wastedTotal: number = 0;

   constructor(private _product: Product){
   }

   public get product() : Product {
      return this._product;
   }

   public get wastedLines() : Array<WastedLine> {
      return this._wastedLines;
   }

   public get wastedTotal() : number {
      return this._wastedTotal;
   }

   public get wasted() : number {
      return this._wasted;
   }

   public set wasted(wasted: number) {
      this._wasted = wasted;
   }

   public addWastedLine(wastedLine: WastedLine){
      if (wastedLine.product.id !=  this._product.id){
         throw new Error(
            'Invalid wasted line for this product'
          );
      }

      this._wastedTotal = this._wastedTotal + wastedLine.wasted;
      this._wastedLines.push(wastedLine);
   }
}