import { Product } from './product.model';

export class WastedLine {

   private _product: Product;
   private _date: Date;
   private _wasted: number = 0;
	
	constructor(product: Product, date: Date, wasted: number){
      this._product = product;
      this._date = date;
      this._wasted = wasted;
	}

   public get product(){
      return this._product;
   }

   public get date(){
      return this._date;
   }

   public get wasted(){
      return this._wasted;
   }

   public set wasted(wasted: number){
      this._wasted = wasted;
   }
}