import { Component, OnInit, NgZone } from '@angular/core';
import { ProductionService } from 'src/services/production.service';
import { ProductionProduct } from 'src/models/production-product.model';

@Component({
  selector: 'app-produccion-component',
  templateUrl: './produccion.component.html',
  styleUrls: ['./produccion.component.css']
})
export class ProduccionComponent implements OnInit {

   private _processing: boolean = false;
   private _allProduction: Array<ProductionProduct> = [];

   constructor(private productionService: ProductionService, private ngZone: NgZone) { }

   public get allProduction(){
      return this._allProduction;
   }

   public get processing(){
      return this._processing;
   }

   ngOnInit() {
      this.productionService.initProductionServiceService().then(()=>{
         this.ngZone.run(() => {
            this._allProduction = this.productionService.allProduction;
         });}
      );
   }

   public saveProduction(){
      this.productionService.saveProduction().then(()=>{
         this.ngZone.run(() => {
            this._allProduction = this.productionService.allProduction;
         });}
      );
   }
}