export class Product {
	nombre: string;
	precio: number = -1;
	id: number; // index 
   tipo: string;
   order: number;

	constructor(id: number, nombre: string, precio: number, tipo: string, order: number){
		this.id = id;
      this.nombre = nombre;
      this.tipo = tipo;

		// if price is less than zero then we treat them all as null
		if (precio >= 0) {
			this.precio = precio;
		}

		if (!order){
			order = -1;
		}
		this.order = order;
	}

	public getRowForCatalogs() : Array<any> {
		var row = new Array();
		row.push(this.id);
		row.push(this.nombre);
		row.push(this.precio);
		row.push(this.order);
		return row;
	}

	static createEmptyProduct() : Product{
		return new Product(-1, "",  -1, "", -1);
   }
   
   public isOwned(): boolean{
      return  "propio" == this.tipo.toLowerCase();
   }
}