import { Product } from './product.model';
import { ProductionLine } from './production-line.model';

export class ProductionProduct {

   private _productionLines: Array<ProductionLine> = [];
   private _production: number = 0;
   private _productionTotal: number = 0;

   constructor(private _product: Product){
   }

   public get product() : Product {
      return this._product;
   }

   public get productionLines() : Array<ProductionLine> {
      return this._productionLines;
   }

   public get productionTotal() : number {
      return this._productionTotal;
   }

   public get production() : number {
      return this._production;
   }

   public set production(production: number) {
      this._production = production;
   }

   public addProductionLine(productionLine: ProductionLine){
      if (productionLine.product.id !=  this._product.id){
         throw new Error(
            'Invalid product line for this product'
          );
      }

      this._productionTotal = this._productionTotal + productionLine.production;
      this._productionLines.push(productionLine);
   }

}