import { Injectable } from '@angular/core';
import { ProductionLine } from 'src/models/production-line.model';
import { GapiSession } from 'src/infrastructure/sessions/gapi.session';
import { BaseService } from './base.service';
import { ProductService } from './product.service';
import { ProductionProduct } from 'src/models/production-product.model';

@Injectable({
  providedIn: 'root'
})
export class ProductionService extends BaseService {

   private static RANGE_PRODUCTION = 'ControlProduccion!A2:C';
   private static RANGE_PRODUCTION_ADD_ROW = 'ControlProduccion!A2';
   private static CONTROL_PRODUCCION_ID = 961378869;

   private _allProduction: Array<ProductionProduct> = [];
   private _initProductionServicePromise: PromiseLike<any>;
   private _initProductionServicePromiseSolved: boolean;

   constructor(private gapiSession: GapiSession, private productService: ProductService) {
      super();
   }

   public get allProduction() : Array<ProductionProduct> {
      return this._allProduction;
   }

   public initProductionServiceService() : PromiseLike<any> {
      if (!this._initProductionServicePromise) {
         this._initProductionServicePromise = this.productService.initProductService().then(()=> {return this._loadProductionLines();});
      }

      if (!this._initProductionServicePromiseSolved){
         return this._initProductionServicePromise;
      }else{
         Promise.resolve("initialized");
      }
   }

   private _loadProductionLines(): Promise<any> {
      return this.gapiSession.initClient("Production Service").then(() => 
         {
            return gapi.client.request(
               {
                  method: 'GET',
                  // Returns a range of values from a spreadsheet => https://developers.google.com/sheets/api/reference/rest/v4/spreadsheets.values/get
                  path: `https://sheets.googleapis.com/v4/spreadsheets/${BaseService.SPREADSHEET_ID}/values/${ProductionService.RANGE_PRODUCTION}?valueRenderOption=${BaseService.VALUE_RENDER_OPTION}&dateTimeRenderOption=${BaseService.DATE_TIME_RENDER_OPTION}`
               }
            ).then((productionRange)=>{
               this.productService.allOwnedProducts.forEach(
                  p => {
                     this._allProduction.push(new ProductionProduct(p));
                  }
               );

               this._initProductionServicePromiseSolved = true;
               var productionList = productionRange.result["values"];
               if (productionList) {
                  
                  for (let i = 0; i < productionList.length; i++) {
                     var productionLineFromSheet = productionList[i];

                     // 0 = Id Producto, 2 = Fecha,  1 = Producido
                     var productionByProduct = this._allProduction.find(pl => pl.product.id == productionLineFromSheet[0]);
                     if (productionByProduct){
                        var productionLine = new ProductionLine(productionByProduct.product, productionLineFromSheet[1], productionLineFromSheet[2]);
                        productionByProduct.addProductionLine(productionLine);
                     }else{
                        console.warn("A ProductionProduct was not found. Was the ControlProduccion modified manually?. Check all products in that list are of Tipo Propio");
                     }
                  }
               }
            });
         }
      );
   }

   public saveProduction(): PromiseLike<any> {

      let productionListToSave = new Array();
      this._allProduction.forEach( p => {
         if (p.production > 0){
            p.addProductionLine(new ProductionLine(p.product, new Date(), p.production));
            p.production = 0;
         }
         p.productionLines.forEach(
            pl => {
               var row = new Array();
               row.push(pl.product.id);
               row.push(pl.date);
               row.push(pl.production);
               productionListToSave.push(row);
            }
         );
      });

		// Registers the Efectivo and the Tarjeta
		return gapi.client.request(
			{
            method: 'POST',
            // Sets values in one or more ranges of a spreadsheet => https://developers.google.com/sheets/api/reference/rest/v4/spreadsheets.values/batchUpdate
            path: `https://sheets.googleapis.com/v4/spreadsheets/${BaseService.SPREADSHEET_ID}/values:batchUpdate`, 
            body: {
               valueInputOption: BaseService.VALUE_INPUT_OPTION,
               data: [
                  {
                     range: ProductionService.RANGE_PRODUCTION_ADD_ROW,
                     values: productionListToSave
                  }
               ]
            }			
         }
		);
	}
}